// mui
import TableCell from '@mui/material/TableCell';
// sdk
import { BookSeries } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export const TableHeader = [
  { id: 'name', label: 'Tên bộ sách', flex: 1 },
  { id: 'description', label: 'Mô tả', flex: 1 },
  { id: 'createdAt', label: 'Ngày tạo', flex: 1, align: 'center' },
  { id: 'updatedAt', label: 'Ngày cập nhật', flex: 1, align: 'center' },
  { id: 'action', label: 'Hành động', flex: 1, align: 'right' },
];

export const TableRows = (row: BookSeries) => {
  return (
    <>
      <TableCell>{row?.name}</TableCell>

      <TableCell>{row?.description}</TableCell>
    </>
  );
};
