// react
import { useEffect, useMemo } from 'react';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// @mui
import LoadingButton from '@mui/lab/LoadingButton';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
// components
import FormProvider, { BlockItem, RHFTextField } from 'src/components/hook-form';
import { useSnackbar } from 'src/components/snackbar';
// api
import { createBookSery, updateBookSery } from 'src/api/controllers/book';
// context
import { useTableContext } from '../../table/context';

// ----------------------------------------------------------------------

type Props = {
  open: boolean;
  onClose: VoidFunction;
};

export default function TypeForm({ open, onClose }: Props) {
  const {
    _table_row_selected: currentValues,
    onSetTableRowSelected,
    onCreateNewRow,
    onUpdateRow,
  } = useTableContext();

  const isEdit = !!currentValues;

  const { enqueueSnackbar } = useSnackbar();

  const Schema = Yup.object().shape({
    name: Yup.string().required('Bạn chưa nhập tên bộ sách !'),
    description: Yup.mixed(),
  });

  const defaultValues = useMemo(
    () => ({
      name: currentValues?.name || '',
      description: currentValues?.description || '',
    }),
    [currentValues]
  );

  const methods = useForm({
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  useEffect(() => {
    reset(defaultValues);
  }, [reset, currentValues, defaultValues]);

  const onSubmit = handleSubmit(async (data: any) => {
    try {
      if (!isEdit) {
        const created = await createBookSery(data);

        onCreateNewRow(created);

        enqueueSnackbar('Đã thêm dữ liệu bộ sách mới !');
      } else {
        const updated = await updateBookSery(data, currentValues.id);

        onUpdateRow(updated);

        enqueueSnackbar(`Đã cập nhật bộ sách ${data.name} !`);
      }
      reset();
      handleClose();
    } catch (error) {
      enqueueSnackbar('Đã có lỗi xảy ra ! Vui lòng thử lại !', { variant: 'error' });
    }
  });

  const handleClose = () => {
    onSetTableRowSelected(null);
    onClose();
  };

  return (
    <Dialog
      fullWidth
      maxWidth={false}
      open={open}
      onClose={handleClose}
      PaperProps={{
        sx: { maxWidth: 720 },
      }}
    >
      <FormProvider methods={methods} onSubmit={onSubmit}>
        <DialogTitle>
          {isEdit ? `Cập nhật dữ liệu bộ sách ${currentValues?.name}` : 'Thêm dữ liệu bộ sách mới'}
        </DialogTitle>

        <DialogContent>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Stack spacing={2}>
                <BlockItem label="Tên bộ sách :" required>
                  <RHFTextField name="name" label="Tên bộ sách..." />
                </BlockItem>

                <BlockItem label="Mô tả bộ sách :">
                  <RHFTextField name="description" multiline rows={4} label="Mô tả bộ sách..." />
                </BlockItem>
              </Stack>
            </Grid>
          </Grid>
        </DialogContent>

        <DialogActions>
          <Button variant="outlined" onClick={handleClose}>
            Huỷ bỏ
          </Button>

          <LoadingButton type="submit" color="primary" variant="contained" loading={isSubmitting}>
            {isEdit ? 'Cập nhật dữ liệu' : 'Thêm dữ liệu mới'}
          </LoadingButton>
        </DialogActions>
      </FormProvider>
    </Dialog>
  );
}
