// react
import { useEffect } from 'react';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
// api
import useGetBookSeries from 'src/api/controllers/book';
// route
import { paths } from 'src/routes/paths';
// sdk
import { BookSeries } from '@iit/sdk/dist/types';
// utils
import { fDateTime } from 'src/utils/format-time';
// table
import TableData from '../../table/table-data';
// context
import { useTableContext } from '../../table/context';
import TypeForm from './book-form';
import { TableHeader, TableRows } from './book-table-item';

// ----------------------------------------------------------------------

export default function BookView() {
  const {
    _table_data,
    _table_open_form,
    onSetOpenForm,
    //
    onSetTableData,
    onSetTableHeader,
    onSetTableRows,
    onSetTableRowCollapse,
    onSetExportTableData,
    onSetConfigTable,
  } = useTableContext();

  const { data: books } = useGetBookSeries();

  useEffect(() => {
    onSetTableData(books);

    onSetConfigTable({
      table_name: 'bookseries',
      add_new: true,
      //
      filter_by_date: true,
      filter_by_type: false,
      filter_by_search: true,
      //
      import_data: false,
      export_data: true,
      //
      table_selected: true,
      allocation_student: false,
      delete_all: true,
      collapse_item: false,
      //
      edit_row: true,
      delete_row: true,
    });

    onSetTableHeader(TableHeader);
    onSetTableRows(TableRows);
    onSetTableRowCollapse(null);
  }, [books]);

  useEffect(() => {
    onSetExportTableData(
      _table_data?.map((book: BookSeries) => ({
        'Tên bộ sách': book?.name,
        'Mô tả bộ sách': book?.description,
        'Ngày tạo': fDateTime(book?.createdAt),
        'Ngày cập nhật': fDateTime(book?.updatedAt),
      }))
    );
  }, [_table_data]);
  return (
    <>
      <CustomBreadcrumbs
        heading="Quản lý bộ sách"
        links={[
          { name: 'Danh mục' },
          {
            name: 'Danh mục',
            href: paths.home.category.book,
          },
          { name: 'Quản lý bộ sách' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <TableData />

      <TypeForm open={_table_open_form} onClose={() => onSetOpenForm(false)} />
    </>
  );
}
